:root {
  --background-color: #383838;
  --text-color: #fff;
  --color-white: #fff;
  --color-graphite-dark: #2f2f2f;
  --color-graphite: #383838;
  --color-text-secondary: #555;
  --color-bg-light: #f9f9f9;
  --color-bg-hover: #f1f1f1;
  --color-border: silver;
  --color-shadow: #0000000d;
  --color-accent: yellow;
  --container-max-width: 1200px;
}

.header {
  background-color: var(--background-color);
  border-bottom: var(--color-accent) solid 1px;
}

body {
  background-color: var(--background-color);
}

.work-schedule {
  background: #fff;
  border-radius: 14px;
  max-width: 300px;
  padding: 18px 20px;
  font-family: Inter, sans-serif;
  box-shadow: 0 4px 14px #0000000d;
}

.contacts {
  margin-top: 30px;
}

.work-schedule h3 {
  color: #111;
  border-bottom: 1px solid #eee;
  margin-top: 0;
  padding-bottom: 8px;
  font-size: 18px;
}

.schedule-list {
  margin: 12px 0;
}

.schedule-list div {
  color: #333;
  padding: 4px 0;
  font-size: 15px;
}

.today-status {
  margin-top: 10px;
  font-size: 15px;
  font-weight: 600;
}

#today-status {
  font-weight: 700;
}

.open {
  color: #1aaf5d;
}

.closed {
  color: #e63946;
}

span {
  color: #fff;
}

.link {
  color: #000;
  text-align: center;
  background-color: #ff0;
  border-radius: 8px;
  width: max-content;
  margin-top: 30px;
  margin-left: auto;
  padding: 10px;
  display: block;
}

.ramka {
  border: 2px solid #ff0;
  border-radius: 10px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 10px;
  padding-left: 10px;
  display: block;

  @media screen and (width >= 1200px) {
    width: 500px;
    padding-bottom: 30px;
    padding-left: 30px;
  }
}

.contacts__flex {
  flex-direction: column;
  align-items: flex-start;
  gap: 50px;
  display: flex;

  @media screen and (width >= 1200px) {
    flex-direction: row;
  }
}

.socials__number-phone {
  color: #fff;
}

.social-list {
  gap: 12px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.social-list li {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  overflow: hidden;
}

.social-list li a {
  background-color: #0000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  transition: background-color .3s;
  display: flex;
}

.social-list li a:hover {
  background-color: #ff0;
}

.social-list li img {
  width: 20px;
  height: 20px;
  display: block;
}

.socials {
  background-color: gray;
  border-radius: 30px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin-top: 50px;
  padding: 20px;
  display: flex;

  @media screen and (width >= 1200px) {
    flex-direction: row;
  }
}

.socials__img {
  color: #fff;
  fill: #fff;
}

li a {
  fill: #fff;
  transition: color .3s;
}

li:hover a .icon {
  fill: #000;
}
